const formatDistanceLocale = {
  lessThanXSeconds: {
    one: "  ",
    two: "  ",
    threeToTen: "  {{count}} ",
    other: "  {{count}} ",
  },

  xSeconds: {
    one: " ",
    two: "",
    threeToTen: "{{count}} ",
    other: "{{count}} ",
  },

  halfAMinute: " ",

  lessThanXMinutes: {
    one: "  ",
    two: "  ",
    threeToTen: "  {{count}} ",
    other: "  {{count}} ",
  },

  xMinutes: {
    one: " ",
    two: "",
    threeToTen: "{{count}} ",
    other: "{{count}} ",
  },

  aboutXHours: {
    one: "  ",
    two: " ",
    threeToTen: "{{count}}  ",
    other: "{{count}}  ",
  },

  xHours: {
    one: " ",
    two: "",
    threeToTen: "{{count}} ",
    other: "{{count}} ",
  },

  xDays: {
    one: " ",
    two: "",
    threeToTen: "{{count}} ",
    other: "{{count}} ",
  },

  aboutXWeeks: {
    one: "  ",
    two: " ",
    threeToTen: "{{count}}  ",
    other: "{{count}}  ",
  },

  xWeeks: {
    one: " ",
    two: "",
    threeToTen: "{{count}} ",
    other: "{{count}} ",
  },

  aboutXMonths: {
    one: "  ",
    two: " ",
    threeToTen: "{{count}}  ",
    other: "{{count}}  ",
  },

  xMonths: {
    one: " ",
    two: "",
    threeToTen: "{{count}} ",
    other: "{{count}} ",
  },

  aboutXYears: {
    one: "  ",
    two: " ",
    threeToTen: "{{count}}  ",
    other: "{{count}}  ",
  },

  xYears: {
    one: " ",
    two: "",
    threeToTen: "{{count}} ",
    other: "{{count}} ",
  },

  overXYears: {
    one: "  ",
    two: "  ",
    threeToTen: "  {{count}} ",
    other: "  {{count}} ",
  },

  almostXYears: {
    one: "   ",
    two: "  ",
    threeToTen: "  {{count}} ",
    other: "  {{count}} ",
  },
};

export const formatDistance = (token, count, options) => {
  const usageGroup = formatDistanceLocale[token];
  let result;
  if (typeof usageGroup === "string") {
    result = usageGroup;
  } else if (count === 1) {
    result = usageGroup.one;
  } else if (count === 2) {
    result = usageGroup.two;
  } else if (count <= 10) {
    result = usageGroup.threeToTen.replace("{{count}}", String(count));
  } else {
    result = usageGroup.other.replace("{{count}}", String(count));
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return " " + result;
    } else {
      return " " + result;
    }
  }

  return result;
};
